<template>
  <div id="login" class="formPage flex align center">
    <div class="userForm shadownItemBox flex between stretch">
      <div class="leftBg"></div>

      <div class="mainBox">
        <div class="title">WMC 用户登录</div>

        <div class="formBox">


          <el-tabs v-model="activeName">
            <el-tab-pane label="电话登录" name="phone"></el-tab-pane>
            <el-tab-pane label="邮箱登录" name="email"></el-tab-pane>
          </el-tabs>

          <div class="inputGroup flex align">
            <i class="icon user"></i>
            <getArea v-show="activeName=='phone'" :default="form.area" @setVal="x=>form.area=x" />
            <input type="text" :placeholder="`请输入${activeName=='phone'?'电话':'邮箱'}号码`" v-model="form.phoneOrEmail">
          </div>
          <!-- inputGroup -->

          <div class="inputGroup flex align">
            <i class="icon pass"></i>
            <input :type="passType?'password':'text'" placeholder="请输入密码" v-model="form.password">
            <i class="icon" :class="passType?'eyeClose':'eye'" @click="passType=!passType"></i>
          </div>
          <!-- inputGroup -->

          <div class="themBtn" @click="login" v-loading="load">登&nbsp;录</div>


          <div class="other flex align between">
            <div class="pointer" @click="gopage('/forgetPass')">忘记密码</div>
            <div><span>没有平台帐号？</span>&nbsp;&nbsp;<a @click="gopage('/register')">立即注册</a></div>
          </div>

        </div>
        <!-- formBox -->

      </div>
      <!-- main -->

    </div>
    <!-- userForm -->
  </div>
  <!-- index -->
</template>

<script>
  import {
    mapMutations,
    mapState,
    mapActions
  } from 'vuex'
  import {
    login
  } from '../data/apis';

  import getArea from '@com/common/getArea'


  export default {
    data() {
      return {
        passType: true,
        activeName: "phone",
        form: {
          "area": "86",
          "password": "123123123",
          "phoneOrEmail": "15288888888"
        },

        load:false
      }
    },
    components: {
      getArea
    },
    computed: {
      ...mapState(['userInfo'])
    },
    methods: {
      ...mapMutations(['setToken']),
      ...mapActions(['actionUserInfo']),
      async login() { //登录
        if (!this.form.phoneOrEmail) {
          return this.$message.error('请输入电话或邮箱号码');
        }
        if (!this.form.password) {
          return this.$message.error('请输入密码');
        }

        let res = Object.assign({},this.form);
        
        if(this.activeName=='email'){
          res.area = null;
        }

        this.load = true;

        try {

          let {message,data} = await login(res);
          this.$message.success(message);
          this.setToken(data)
          await this.actionUserInfo();
          setTimeout(() => {
            // this.goback();
            this.gopage_re('/home')
            this.load = false;
          }, 1000);

        } catch (error) {
          this.load = false;
        }//end try
        
      } //login
    },
    mounted() {

    }, //mounted
    watch: {
      activeName() {
        this.form.password = this.form.phoneOrEmail = '';
      }
    }
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../styles/Val";

</style>



